body
  background-color: yellowgreen
  padding: 0
  margin: 0
  height: 100vh
  width: 100vw
  display: flex
  flex-wrap: wrap

  header
    background-color: green
    height: calc(25% - 2rem)
    width: calc(100% - 2rem)
    margin: 1rem

  section
    background-color: bisque
    height: 60%
    width: calc(80% - 2rem)
    margin: 0 1rem

  aside
    background-color: cornflowerblue
    height: 60%
    width: calc(20% - 1rem)

  footer
    background-color: cadetblue
    height: calc(15% - 2rem)
    width: calc(100% - 2rem)
    margin: 1rem
